html {
  font-size: 62.5%;
  min-height: 100vh;
}

.App {
  font-size: 1.5rem;
  display: flex;
  flex-direction: row;
  max-width: 100vw;
  min-height: 100vh;
  @media screen and (max-width: 1030px) {
    flex-direction: column;
  }
}

.site-content {
  width: 100%;
  min-height: 400px;
  overflow-x: hidden;
  position: relative;
}

.bio-div {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  width: 400px;
  min-height: 100vh;
  border-bottom: solid 1px #ddd;
  position: relative;
  left: 0;
  padding: 30px 10px 20px 10px;

  background-color: rgba(145, 13, 13, 0.8);
  color: #fff;

  @media screen and (max-width: 1030px) {
    width: 100vw;
    min-height: auto;
    flex-wrap: nowrap;
    padding: 0px;
    align-items: flex-start;
  }

  .styled-link {
    text-decoration: none;
    color: white;
    @media screen and (max-width: 1030px) {
      align-self: center;
    }
  }

  .img-div {
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
  }
  .img-div:visited {
    text-decoration: none;
  }
  .introduction {
    max-width: 100%;
  }
  .mobile-nav {
    align-self: flex-start;
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 1030px) {
      flex-direction: row-reverse;
      justify-content: space-between;
      width: 100vw;
      border-top: 1px solid #c0c0c0;
      padding: 0px;
      margin: 14px 0px;
    }
  }

  .nav {
    align-self: flex-start;
    .nav-link {
      li {
        list-style: none;
        line-height: 4rem;
        color: #c0c0c0;
        a {
          color: #c0c0c0;
          text-decoration: none;
        }
      }
      @media screen and (max-width: 1030px) {
        display: none;
      }
    }
    .nav-link .nav-link li :active,
    :hover {
      color: #fff;
      a {
        list-style: none;
        text-decoration: none;
      }
    }
  }
  .social-media {
    align-self: flex-start;
    padding: 0px 0px 0px 40px;
    height: 30px;
    .social-media-title {
      @media screen and (max-width: 1030px) {
        display: none;
      }
    }
  }
  .social-media-list {
    display: flex;
    flex-direction: row;
    list-style: none;
    color: #c3c3c3;
    font-size: 1.4rem;
    justify-content: flex-start;
    padding: 0px;
    li {
      margin: 5px 5px;
    }
  }
}

.active-page::after {
  content: "    >";
  color: white;
}

.portfolio {
  padding: 0px 30px;
  position: absolute;
  width: 100%;
  min-height: 100%;
  height: auto;
  background-color: #fff;
  @media screen and (max-width: 1030px) {
    position: relative;
    top: 0%;
    margin: 0px;
    padding: 10px 30px;
  }

  .portfolio-heading {
    text-align: left;
    width: 80%;
    .portfolio-title {
      font-size: 3rem;
      margin-top: 10px;
      @media screen and (max-width: 1030px) {
        margin-top: 0px;
      }
    }
  }

  .portfolio-img {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    width: 50%;
    @media (max-width: 700px) {
      width: 100%;
    }
  }
}

.portfolio-items--panel {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: stretch;
  position: relative;

  @media screen and (max-width: 680px) {
    flex-direction: column;
    justify-content: flex-start;
  }

  .portfolio-items--panel__date {
    color: #a9a9a9;
    flex-basis: 80px;
    border-right: 1px solid #d3d3d3;
    text-align: right;
    padding-right: 15px;
    flex-shrink: 0;
    @media screen and (max-width: 680px) {
      align-self: flex-start;
      border: none;
      flex-basis: 20px;
    }
  }
  .portfolio-items--panel__date ::after {
    content: " ";
    position: absolute;
    width: 7px;
    height: 7px;
    background-color: rgba(145, 13, 13, 1);
    display: block;
    top: 5px;
    // left: 111px;
    right: -2.5px;
    @media screen and (max-width: 680px) {
      width: 0px;
    }
  }
  .portfolio-items--panel__main {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 15px;
    border-bottom: 1px solid #d3d3d3;
    @media screen and (max-width: 1030px) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }
  .portfolio-items--panel__main_inner {
    flex-basis: 400px;
    margin: 0px 10px;
  }

  .portfolio-items--panel__heading {
    .main-heading {
      display: block;
      font-weight: 600;
      font-size: 2rem;
      margin-bottom: 2rem;
    }
    .sub-heading {
      display: block;
      font-weight: 300;
    }
  }

  .portfolio-items--panel__technologies {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-bottom: 15px;
  }

  .technology__title {
    color: #c3c3c3;
    font-weight: 400;
  }

  .technology__item {
    color: #fff;
    width: auto;
    height: auto;
    background-color: rgba(145, 13, 13, 0.7);
    padding: 5px;
    border-radius: 3px;
    margin: 5px 5px;
  }

  .portfolio-items--panel__button {
    background-color: rgba(145, 13, 13, 0.9);
    color: #fff;
    padding: 5px;
    a {
      text-decoration: none;
      color: #fff;
    }
    .portfolio-items--panel__button a:visited,
    :active,
    :hover {
      text-decoration: none;
      color: #fff;
    }
  }

  .portfolio-items--panel__img {
    flex-basis: 400px;
    height: auto;
    margin: 0px 10px;
    img {
      width: 80%;
    }
    @media screen and (max-width: 680px) {
      flex-basis: 300px;
    }
  }
  .portfolio-items--panel .portfolio-items--panel__main:last-child {
    border-bottom: none;
  }
}

.homepage {
  font-size: 1.5rem;
  position: relative;
  max-width: 80%;
  left: 50%;
  top: 30%;
  transform: translate(-50%, -50%);
  margin: 20px 0px;
  h1 {
    color: #fff;
    text-shadow: -0.5px 0 black, 0 1px black, 1px 0 black, 0 -0.5px grey;
    .heading-primary {
      font-weight: 800;
    }
    .heading-secondary {
      font-weight: 400;
    }
  }
  .homepage--btn {
    font-size: 3rem;
    color: #fff;
    border-radius: 5px;
    border: 1px solid;
    background-color: transparent;
  }
  @media screen and (max-width: 1030px) {
    position: relative;
    width: 100vw;
    left: 50%;
    top: 60%;
    transform: translate(-50%, 10%);
  }
}

.carousel-indicators li {
  border: 1px solid #000;
  border-radius: 50px;
}

.carousel-inner {
  .carousel-item {
    .carousel-caption {
      color: #000;
      width: 50%;
      text-align: center;
      left: 50%;
      transform: translateX(-50%);
      h3 {
        font-size: 1.1rem;
      }
      p {
        font-size: 0.7rem;
      }
    }
  }
}

.project-panel {
  max-width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  .project-panel--item {
    width: 280px;
    .project-panel--img-div {
      display: block;
      height: 150px;
    }
    .project-panel--img {
      width: 85%;
    }
  }
}

.navbar {
  padding: 10px 30px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;
  width: 70%;
  height: auto;
  top: 50%;
  left: 50%;
  padding: 15px;
  transform: translate(-50%, -50%);
  .close-button {
    position: absolute;
    border-radius: 30px;
    top: -10px;
    right: -10px;
  }
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: relative;
  width: 32px;
  height: 24px;
  top: 10px;
  left: 10px;

  @media screen and (min-width: 1030px) {
    display: none;
  }
  .bm-burger-button & {
    height: 0px;
  }
}

a,
:visited,
:active {
  text-decoration: none;
  color: #c3c3c3;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #c3c3c3;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #c3c3c3;
}

/* General sidebar styles */
.bm-menu-wrap {
  position: fixed !important;
  left: 0 !important;
}

.bm-menu {
  background: #373a47;
  padding: 2.5em 1.5em;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}
